@import url(https://fonts.googleapis.com/css2?family=Rokkitt&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #525252;
  height: 100%;
  min-height: 940px;
}

.background__container {
}

.background__wrapper {
  width: 1200px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  margin: 0 auto;
}

.coin__symbol {
  text-transform: uppercase;
  font-weight: 700;
}

.input__container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 24px 0px;
}
.input__search {
  height: 40px;
  width: 300px;
  font-size: 1.5rem;
}

.table__container {
  border-spacing: 0;
  font-family: "Rokkitt", serif;
  font-size: 1.2rem;
  color: #fff;
  background: #525252;
}

.table__header {
  width: 200px;
  padding: 8px 0px;
  border-bottom: 3px solid #000;
  border-top: 3px solid #000;
}

.table__header::after {
  content: "▲▼";
}

.table__column {
  height: 30px;
}

.table__row {
  border-bottom: 2px solid #000;
  padding: 6px 0px;
}

.red {
  color: rgb(255, 24, 24);
}

.green {
  color: rgb(148, 255, 42);
}

.sort-desc {
  width: 200px;
}

.sort-desc::after {
  content: "▼";
}

.sort-asc {
  width: 200px;
}

.sort-asc::after {
  content: "▲";
}

.banner__container {
  height: 300px;
}

